import React from "react";
import styled from "styled-components";

const Box = styled.div`
  position: fixed;
  width: 100vw;
  top: 0;
  z-index: 90000;
  height: 5px;
  background-color: rgba(0, 0, 0, 0.1);
  @media screen and (max-width: 1000px) {
    height: 4px;
    transition: 0.5s ease-in-out;
    top: ${(props) => (props.visible ? "50px" : "0px")};
  }
  @media screen and (max-width: 479px) {
    height: 3px;
  }
`;

const ProgressBar = styled.div`
  height: 5px;
  background-color: rgba(0, 0, 0, 1);
  width: ${(props) => props.scrollRatio};
  @media screen and (max-width: 1000px) {
    height: 4px;
  }
  @media screen and (max-width: 479px) {
    height: 3px;
  }
`;

const Header = styled.div`
  z-index: 90001;
  position: fixed;
  top: -50px;
  width: 100%;
  height: 50px;
  font-family: Outfit, sans-serif;
  font-weight: 800;
  background-color: rgba(255, 255, 255, 1);
  display: flex;
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
  @media screen and (max-width: 1000px) {
    transition: 0.5s ease-in-out;
    top: ${(props) => (props.visible ? "0px" : "-50px")};
  }
  @media screen and (max-width: 479px) {
    padding-left: 20px;
    padding-right: 20px;
  }
`;

const ScrollBox = ({ scrollRatio, scrollHeight, pathname, stage }) => {
  const capitalizeFirstLetter = (string) => {
    return string.charAt(0).toUpperCase() + string.slice(1);
  };
  const capitalizedPathname = capitalizeFirstLetter(pathname);
  return (
    <>
      <Box visible={scrollHeight > 840 ? true : false} className="dD">
        <ProgressBar
          scrollRatio={scrollRatio}
          visible={scrollHeight > 840 ? true : false}
        ></ProgressBar>
      </Box>
      <Header visible={scrollHeight > 840 ? true : false} className="dD">
        {stage === "Home"
          ? "Montage.gif"
          : stage === "About"
          ? "Get My Montage.gif"
          : capitalizedPathname + ".gif"}
      </Header>
    </>
  );
};

export default ScrollBox;
